.footer {
  background-color: $black;
  color: $wight;
}

.footer__container {
  display: flex;
  flex-wrap: wrap;
  padding: 100px 15px;

  @media (max-width: $desktop-width) {
    padding: 60px 150px 60px 45px;
    margin-right: auto;
  }

  @media (max-width: $tablet-width) {
    padding: 60px 15px;
    flex-direction: column;
  }
}

.footer__subscribe {
  flex-basis: 50%;
  margin-right: 88px;

  @media (max-width: $desktop-width) {
    flex-basis: 100%;
    margin-right: 0;
    margin-bottom: 25px;
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 50px;
  }
}

.footer__social {
  margin-left: auto;

  @media (max-width: $tablet-width) {
    margin: 0;
    margin-top: 50px;

  }


}
